.Comment-item {
  position:relative;
}

.Comment-item:hover .Btn-delete{
  display: block;
}

.Comment-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
  -webkit-transition: all ease .3s;
  -o-transition: all ease .3s;
  transition: all ease .3s;
}

.Comment-container:hover .Btn-edit{
  opacity: 1;
  display: block;
}

.Comment-message {
  margin-right: 30px !important;
}

.Btn-edit {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, .5);
  cursor: pointer;
  opacity: 0;
  display: none;
  -webkit-transition: all ease .3s;
  -o-transition: all ease .3s;
  transition: all ease .3s;
}

.Btn-edit:hover {
  color: #FFD740;
  font-size: 1.4rem;
}

.Btn-delete {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
  color: rgba(0, 0, 0, .3);
  cursor: pointer;
  font-size: 1.1rem;
  -webkit-transition: all ease .35s;
  -o-transition: all ease .35s;
  transition: all ease .35s;
}

.Btn-delete:hover {
  color: #F44336;
  font-size: 1.3rem; 
}



